import React, { FC, useContext, useEffect, useState } from 'react';
import { Link } from 'gatsby';
import { InternalLinks } from '../../../utils/links';
import BulbIcon from '../Icons/BulbIcon/BulbIcon';
import SuccessFormSubmitMessage from '../SuccessFormSubmitMessage/SuccessFormSubmitMessage';
import {
  Description,
  Header,
  Icon,
  StyledSubscribeBlock,
  SubscribeForm,
  Title,
  Notification,
} from './styled';
import { AppContext } from '../Layout/Layout';
import { ClipLoader } from 'react-spinners';
import { colors } from '../../../utils/colors';
import useFetchHubSpotForms from '../../../hooks/useFetchHubSpotForms';
import { useInView } from 'react-cool-inview';

interface Props {
  title: string;
  description: string;
}

const SubscribeBlock: FC<Props> = ({ title, description }) => {
  const appContext = useContext(AppContext);
  useEffect(() => {
    //@ts-ignore
    window.hbspt?.forms.create({
      region: 'na1',
      portalId: '21630623',
      formId: '9e11a6c7-d24f-4820-9508-06489e3b552b',
      target: '#contactUsSubsrcribeForm',
      onFormSubmit: function () {
        //@ts-ignore
        gtag_report_conversion();
      },
    });
  }, [appContext.hubSpot.isHubSpotFormsLoaded]);

  const { observe, inView } = useInView({});

  useFetchHubSpotForms(
    [inView, appContext.hubSpot.isHubSpotFormsLoaded],
    inView && !appContext.hubSpot.isHubSpotFormsLoaded,
    appContext.hubSpot.setIsHubSpotFormsLoaded,
  );

  return (
    <StyledSubscribeBlock ref={observe}>
      <Header>
        <Title>{title}</Title>
        <Icon>
          <BulbIcon />
        </Icon>
      </Header>
      <Description>{description}</Description>
      <Notification>
        By clicking the submit button below, I hereby agree to and accept Telgorithm’s{' '}
        <Link to={InternalLinks.TermsAndConditions}>terms and conditions</Link>.
      </Notification>
      <SubscribeForm id="contactUsSubsrcribeForm">
        {!appContext.hubSpot.isHubSpotFormsLoaded && (
          <ClipLoader color={colors.primary.lightOrange} />
        )}
      </SubscribeForm>
    </StyledSubscribeBlock>
  );
};

export default SubscribeBlock;
